import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import yumiya from "../views/yumiya";
import Index from "../views/Index";
import summary from "../views/summary";
import content from "../views/content";

Vue.use(VueRouter)

const routes = [//

  {
    path: '/',
    show: true,
    name: "",
    redirect:"/introduce",
    component: Index,
    children:[
      {
        path: '/introduce',
        name: "",
        show: true,
        component: Home
      },
      {
        path: '/gaibu',
        name: "",
        show: true,
        component: yumiya
      }
    ]
  },
  {
    path: '/summary',
    name: "",
    show: true,
    component: summary
  },
  {
    path: '/content',
    show: false,
    name: "content",
    component: content
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
