<template>
  <div :style="note">
    <div class="position">{{name}}</div>
      <div class="Banner" >
        <el-carousel  indicator-position="none" :height="imgHeight+'px'" :weight="imgWidth+'px'" >
          <el-carousel-item v-for="item in imgs" :key="item.id" >
            <img ref="imgH" :src="item.img" class="image" @load="imgLoad" align="center" >
          </el-carousel-item>
        </el-carousel>
      </div>
      <el-divider></el-divider>
      <div class="text1">
        <span>
        </span>
        <el-divider></el-divider>
        <span>5301
        </span><el-divider></el-divider>
        <span>301449
        </span><el-divider></el-divider>
        <span>5014271617
        </span>
      </div>
  </div>
</template>


<script>

export default {

  name: "home",
  data(){
    return{
      note: {
        backgroundImage: "url(" + require("/public/background.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        backgroundClip: "content-box",
        width: "100%",
        height: "100%",
        position: "absolute",
        left: "0px",
      },
      name:"",
      imgHeight:'',
      imgWidth:'',
      imgs:[
        {
          id: 1, img: require('../../public/rollingimage/atago.png')
        },
        {
          id: 2, img: require('../../public/rollingimage/gi.png')
        },
        {
          id: 3, img: require('../../public/rollingimage/jin.png')
        },
        {
          id: 4, img: require('../../public/rollingimage/nobori.png')
        }
      ]
    }
  },
  methods:{
    imgLoad(){
      this.$nextTick(()=>{
        if(document.documentElement.clientWidth > 1024){
          this.imgWidth = document.documentElement.clientWidth * 0.9;
          this.imgHeight = document.documentElement.clientWidth / 1024 * 255 * 0.9;
        }
        else
        {
          this.imgWidth = document.documentElement.clientWidth * 0.9;
          this.imgHeight = document.documentElement.clientWidth / 1024 * 255 * 0.9;
        }
      })
    }
  },
  mounted () {
    this.imgLoad();
    window.addEventListener("resize",this.imgLoad,false);
  },
  destroyed(){
    window.removeEventListener("resize",this.imgLoad,false);
  },
}
</script>

